import { SaleStatus, GoodType, Dinnertime, isFreeze, isWechat, isAdmin, isDelete, chooseAll, positionRight } from "@/config/cfg";
import Vue from "vue";
import { Util } from '@/config/util';
import {
	silverData
}
from './api';
export const silverMethods = {

	//保存设置信息(微信支付设置、支付宝支付设置)
	saveSeting: function(data, obj, fn) {
		silverData.silverSet(data, (resp) => {
			if(resp.ret == 0) {
				obj.validate((valid) => {
					if(valid) {
						fn({
			            message: "收银设置已经保存"
						});
					} else {
						return false;
					}
				});
			} else {
				obj.validate((valid) => {
					return false;
				});
			}
		})
	}

}


