<template>
	<div id="openWechatPay">
		<!--未授权-------------isPremission==false------------------->
		<div class="noPremission" v-if="isPremission==true">
			<div class="text left">
				<p class="bigTop">使用微信支付，需先开通参数配置权限，请联系客服：<span class="blue">0755-2306 0180</span> </p>
				
				<p>若您未在本平台内录入完整的工商和账户信息，您需要准备以下资料：</p>
				<p>1.营业执照注册号、经营范围、营业期限、营业执照扫描件</p>
				<p>2.法人代表姓名、法人手机号码、法人身份证正面和反面扫描件、身份证号码、身份证有效期</p>
				<p>3.《餐饮服务许可证》（《食品卫生许可证》）或者提供实体店铺的三张照片（包含：1. 门头照含店名；2. 店内全景照；3. 店内收银台照）须为彩</p>
				<p>色图片且小于2M，文件格式为bmp、png、jpeg、jpg或gif</p>
				<p>4.开户银行名称、开户名称、银行账号</p>
			</div>
		</div>

		<!--授权-->
		<div class="formConfig" v-else>
			<el-form label-width="152px" :model="noConfigForm" label-position="right" ref="noConfigForm">
				<el-form-item label="支付宝APPID">
					<span>{{noConfigForm.appId}}</span>
				</el-form-item>
				<el-form-item label="RSA私钥">
					<span>{{noConfigForm.passWord}}</span>
				</el-form-item>
				<el-form-item label="支付宝公钥">
					<span>{{noConfigForm.publicKey}}</span>
				</el-form-item>
				<el-form-item label="安全校验码">
					<span>{{noConfigForm.test}}</span>
				</el-form-item>
				<el-form-item label="合作者身份">
					<span>{{noConfigForm.partner}}</span>
				</el-form-item>
				<el-form-item label="支付宝账号">
					<span>{{noConfigForm.accountNum}}</span>
				</el-form-item>
			</el-form>
		</div>

	</div>
</template>

<script>
	import { silverData } from "../../api";
	import { silverMethods } from "../../index";
	import { isSpecial, silverCode } from "@/config/cfg";
	import { Public } from "@/config/public";
	export default {
		props: ["config"],
		data() {
			return {
				isPremission: false,
				isConfig: false,
				noConfigForm: {
					appId: "", //支付宝APPID
					passWord: "", //RSA私钥
					publicKey: "", //公钥
					test: "", //安全校验码
					partner: "", //合作者身份
					accountNum: "" //支付宝账号
				}
			}
		},
		created() {
			this.shopId = Public.getShopid();
			this.isPremission = this.config;
			this.initData()
		},
		methods: {
			//初始化数据
			//初始化数据
			initData() {
				let data = {
					select_alipay_set: 1,
					shop_id: this.shopId,
					pay_way: 2
				}
				silverData.getShopData(data, (resp) => {
					this.noConfigForm.appId =resp.data.alipay==undefined||resp.data.alipay.length==0? "":resp.data.alipay[silverCode.NO].alipay_app_id;
					this.noConfigForm.passWord =resp.data.alipay==undefined||resp.data.alipay.length==0? "": resp.data.alipay[silverCode.NO].private_key;
					this.noConfigForm.publicKey =resp.data.alipay==undefined||resp.data.alipay.length==0? "": resp.data.alipay[silverCode.NO].public_key;
					this.noConfigForm.test =resp.data.alipay==undefined||resp.data.alipay.length==0? "": resp.data.alipay[silverCode.NO].safe_code;
					this.noConfigForm.partner = resp.data.alipay==undefined||resp.data.alipay.length==0? "":resp.data.alipay[silverCode.NO].hz_identity;
					this.noConfigForm.accountNum =resp.data.alipay==undefined||resp.data.alipay.length==0? "": resp.data.alipay[silverCode.NO].alipay_num;
					if(this.noConfigForm.imgSrc !== "") {
						this.isImg = true;
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#openWechatPay {
		margin-top: 87px;
		margin-left: -40px;
	}
	
	.img {
		width: 50px;
		height: 50px;
		margin-right: 30px;
	}
	
	.text-header {
		padding-left: 75px;
	}
	
	img {
		width: 100%;
		height: 100%;
	}
	
	.text {
		width: 80%;
		margin-left: 35px;
	}
	
	p {
		font-size: 12px;
		color: #9B9B9B;
		height: 30px;
		line-height: 30px;
	}
	
	.small {
		font-size: 12px;
		color: darkgray;
	}
	
	.blue {
		font-size: 12px;
		color: #4877E7;
	}
	
	.bigTop {
		height: 50px;
		line-height: 50px;
	}
	
	.notConfig {
		width: 60%;
	}
	
	.img {
		width: 100px;
		height: 100px;
		margin-right: 20px;
		position: relative;
		img {
			width: 100%;
			height: 100%;
		}
	}
	
	.img:hover .mask,
	.img:hover .el-icon-view {
		display: block;
	}
	
	.mask {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.el-icon-view {
		display: none;
		position: absolute;
		opacity: 1;
		top: 40%;
		left: 40%;
		font-size: 20px;
		color: #fff;
		cursor: pointer;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#openWechatPay {
		.el-radio.yes {
			margin-right: 100px;
		}
		.el-button.submitBtn {
			margin: 0 30px 0 200px;
		}
		.el-button.cancelBtn {
			background: #fff;
			color: #4877E7;
		}
		.el-button.downLoad {
			background: #fff;
			color: #666666;
			border: 1px solid #666666;
		}
		.el-form-item__label {
			text-align: right;
			/*padding-left: 0;*/
			padding-right: 47px;
		}
		.formConfig .el-form {
			margin-left: 5px;
			padding-left: 0;
		}
	}
</style>


